import { Box, Button, Paper } from "@mui/material";
import React, { useEffect } from "react";
import List from "@mui/material/List";
import ListItemText from "@mui/material/ListItemText";
import { useDispatch, useSelector } from "react-redux";
import { getProduct } from "../features/basket.slice";
import { createCommande, postCommande } from "../api/api";
import { token } from "../config/config";
const Panier = () => {
  const toggle = useSelector((state) => state.toggle.value);
  const type = useSelector((state) => state.type.value);
  useEffect(() => {
    console.log(toggle[5].heure);
  }, [toggle]);
  const dispatch = useDispatch();
  const panier = useSelector((state) => state.basket.value);
  const [data, setData] = React.useState([]);
  useEffect(() => {
    setData(panier);
    console.log(data);
  }, [panier]);
  const postData = () => {
    console.log(toggle[5].heure);
    const commande = {
      clientName: "test",
      horraire: toggle[5].heure,
      token: token,
    };
    createCommande(commande).then((data) => {
      panier.map((item) => {
        if (item.type === "buger") {
          const commandeProduit = {
            commandeId: data.data.id,
            burgerId: item.id,
            
            
          };
          postCommande(commandeProduit).then((data) => {
            console.log(data);
          });
        } else if (item.type === "soft") {
          const commandeProduit = {
            commandeId: data.data.id,
            softId: item.id,
            token: token,
          
          };
          
          postCommande(commandeProduit).then((data) => {
            console.log(data);
          });
        } else if (item.type === "dessert") {
          const commandeProduit = {
            commandeId: data.data.id,
            dessertId: item.id,
            token: token,

          };
          postCommande(commandeProduit).then((data) => {
            console.log(data);
          });
        
        } else if (item.type === "menu") {
          const commandeProduit = {
            commandeId: data.data.id,
            menuId: item.id,
            token: token,

          };
          postCommande(commandeProduit).then((data) => {
            console.log(data);
          });
        }
        
      });
    });
  };

  return (
    <Box className="panier">
      <Paper
        elevation={24}
        sx={{
          width: "200px",
        }}
      >
        <List
          sx={{
            width: "100%",
            maxWidth: 360,
            bgcolor: "background.paper",
            position: "relative",
            overflow: "auto",
            maxHeight: 300,
            "& ul": { padding: 0 },
          }}
          subheader={<li />}
        >
          <h2>Panier</h2>
          {panier.map((item) => (
            <>
              <li key={`section-${item.id}`}>
                <ul>
                  <ListItemText primary={item.name} />
                </ul>
              </li>
            </>
          ))}
        </List>
        <Button onClick={() => postData()}>Commander</Button>
      </Paper>
    </Box>
  );
};

export default Panier;
