import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);



const animToggle = () => {
    const tl = gsap.timeline();
    tl.fromTo(".anim", {
        opacity: 0,
        x: "-100%",
    }, {
        opacity: 1,
        x: "0%",
        duration: 1,
    
    })
    tl.from(".stagger", {
        opacity: 0,
        y: -100,
        stagger: 0.2,
        duration: 1,
    }
    )
}

const animToggle2 = () => {
    const tl = gsap.timeline();
   tl.to(".anim", {
        opacity: 0,
        x: "-100%",
        duration: 1,
    })
   

}


export { animToggle, animToggle2 };




