import { Box, Button, Paper } from "@mui/material";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toggle, changeValueBurgers,changeValueDesserts,changeValueHoraire,changeValueMenus,changeValueSofts } from "../features/toggle.slice";
import {animToggle} from "../anim/anim";
import { gsap } from "gsap";
import gsapCore from "gsap/gsap-core";
const ClassProducts = () => {

useEffect(() => {
    animToggle();
}, []);

  const dispatch = useDispatch();
  const toggle = useSelector((state) => state.toggle.value);
  console.log(toggle);
  const classProduct = ["Burgers", "Softs", "Desserts", "Menus"];

  const choice = (item) => {
    gsap.to('.anim',{
      duration: 0.5,
      opacity: 0,
      x : '-100%',
      onComplete: () => {
        const value = item;
  
        if (value === "Burgers") {
          dispatch(changeValueBurgers(true));
            dispatch(changeValueDesserts(false));
            dispatch(changeValueMenus(false));
            dispatch(changeValueSofts(false));
        } else if (value === "Softs") {
            dispatch(changeValueSofts(true));
                dispatch(changeValueBurgers(false));
                dispatch(changeValueDesserts(false));
                dispatch(changeValueMenus(false));
        } else if (value === "Desserts") {
            dispatch(changeValueDesserts(true));
                dispatch(changeValueBurgers(false));
                dispatch(changeValueMenus(false));
                dispatch(changeValueSofts(false));
        } else if (value === "Menus") {
            dispatch(changeValueMenus(true));
                dispatch(changeValueBurgers(false));
                dispatch(changeValueDesserts(false));
                dispatch(changeValueSofts(false));
        }
      }
    })
  };
  
  return (
    <Box className="anim"  sx={{
      backgroundColor: "transparent",
      display: "flex",
    }}>
      <Paper elevation={24}  sx={{
        backgroundColor: "transparent",
      }}>
        {classProduct.map((item, index) => (
          <Button
            onClick={() => choice(item)}
            key={index}
            variant="contained"
            sx={{ margin: 1 }}
          >
            {item}
          </Button>
        ))}
      </Paper>
    </Box>
  );
};

export default ClassProducts;
