import { configureStore } from "@reduxjs/toolkit";
import toggleSlice from "../features/toggle.slice";
import heureSlice from "../features/heure.slice";
import basketSlice from "../features/basket.slice";
import typeSlice from "../features/type.slice";

export default configureStore({
    reducer: {
        toggle : toggleSlice,
        heure : heureSlice,
        basket : basketSlice,
        type : typeSlice,

    },
});