import Horaires from './componants/Horaires';
import logo from './logo.svg';
import { useDispatch, useSelector } from "react-redux";
import ClassProducts from './componants/ClassProducts';
import Desserts from './componants/Desserts';
import Burgers from './componants/Burgers';
import Softs from './componants/Softs';
import Menus from './componants/Menus';
import Panier from './componants/Panier';



function App() {
  const dispatch = useDispatch();
  const toggle = useSelector((state) => state.toggle.value);

const toggleValue = toggle[0].toggleHoraires;
const toggleValueBurgers = toggle[1].toggleBurgers;
const toggleValueSofts = toggle[2].toggleSofts;
const toggleValueDesserts = toggle[3].toggleDesserts;
const toggleValueMenus = toggle[4].toggleMenus;

const choiceComponent = () => {
  if (toggleValue !== true) {
    return <Horaires />
  } else if (toggleValueBurgers === true) {
    return  <Burgers />;  ;
  } else if (toggleValueSofts === true) {
    return <Softs />;
  } else if (toggleValueDesserts === true) {
    return <Desserts />;
  } else if (toggleValueMenus === true) {
    return <Menus />;
  } else {
    return <ClassProducts /> ;
};
};



  return (
 <>
  {choiceComponent()}
  <Panier />
  </>
 


  );
}

export default App;
