import { createSlice } from "@reduxjs/toolkit";


export const toggleSlice = createSlice({
    name: 'toggle',
    initialState: {
        value : [{toggleHoraires : false}, {toggleBurgers : false}, {toggleSofts : false}, {toggleDesserts : false}, {toggleMenus : false}, {heure :"" }]
    },
    reducers: {
        toggle: (state, action) => {
            state.value = action.payload
        },
        changeValueHoraire: (state, action) => {
            state.value[0].toggleHoraires = action.payload
        },
        changeValueBurgers: (state, action) => {
            state.value[1].toggleBurgers = action.payload
        },
        changeValueSofts: (state, action) => {
            state.value[2].toggleSofts = action.payload
        },
        changeValueDesserts: (state, action) => {
            state.value[3].toggleDesserts = action.payload
        },
        changeValueMenus: (state, action) => {
            state.value[4].toggleMenus = action.payload
        },
        addHoraire: (state, action) => {
            state.value[5].heure = action.payload
        }
        

    }
})


export const { toggle,  changeValueHoraire, changeValueBurgers,changeValueDesserts,changeValueMenus,changeValueSofts, addHoraire } = toggleSlice.actions
export default toggleSlice.reducer