import axios from "axios";
import { token } from "../config/config";

export const getHoraire = (horaire) => {
  axios
    .get("http://localhost:3001/api/test/commandetest", {
      headers: {
        horaire: "19h",
      },
    })
    .then((response) => {
      console.log(response.data);
    })
    .catch((error) => {
      console.log(error);
    });
};
export const getAllCommandes = () => {
  try {
    return axios.get("http://localhost:3001/api/commandes/selectcommande",
    {
      headers: {
        token: token,
        },
        });
  } catch (error) {
    console.error(error);
  }
};

