import { createSlice } from "@reduxjs/toolkit";

export const heureSlice = createSlice({
    name: "heure",
    initialState: {
        heure : [],
    },
    reducers: {
        addHeure: (state, action) => {
            state.heure.push(action.payload);
        },
    
    }
});
export const { addHeure, } = heureSlice.actions;
export default heureSlice.reducer;