import React, { useEffect, useState } from "react";
import { getAllCommandes } from "../api/api";
import Commandes from "./Commandes";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import { useDispatch, useSelector } from "react-redux";
import { addHeure } from "../features/heure.slice";
import { toggle, changeValueHoraire,addHoraire } from "../features/toggle.slice";
import {animToggle} from "../anim/anim";
import { gsap } from "gsap";

const Horaires = () => {
    const toggles = useSelector((state) => state.toggle.value);
    console.log(toggles);
    const dispatch = useDispatch();
    const [horaire, setHoraire] = useState(false);
    const [element, setElement] = useState([]);
    const [tableau, setTableau] = useState();
    const heures = [
        "19h00",
        "19h15",
        "19h30",
        "19h45",
        "20h00",
        "20h15",
        "20h30",
        "20h45",
        "21h00",
        "21h15",
        "21h30",
        "21h45",
      ];
      const [modalCommande, setModalCommande] = useState(false);
  const array = [];
  const result = [];

  useEffect(() => {
    getAllCommandes().then((data) => {
      setHoraire(data.data);
      console.log(data.data);
    animToggle();
    });
  }, []);
  if (horaire) {
    // console.log(horaire);
    // séparer les horaires différents
    horaire.map((item) => {
      array.push(item.horraire);
    });
    // console.log(array);
    const unique = [...new Set(array)];
    console.log(unique);
    // savoir combien de horraire sont identiques
    unique.map((item) => {
      let count = 0;
      horaire.map((item2) => {
        if (item === item2.horraire) {
          count++;
        }
      });
      result.push({ horraire: item, count: count });
    });
  }
  console.log(result);

  const newHeures = heures.map((item) => {
    let count = 0;
    result.map((item2) => {
      if (item === item2.horraire) {
        count = item2.count;
      }
    });
    return { horraire: item, count: count };
  });
  console.log(newHeures);

  const toggle = (heure, tableau) => {
    return <Commandes heure={heure} data={tableau} />;
  };
const date = new Date().toLocaleString("us", {timeZone: "Europe/Paris"})
  const animToggle2 = (item) => {
    dispatch(addHoraire(item.horraire));
    // const newH = item.horraire.replace("h",':')  ;
    
    const newH = item.horraire+":00".replace("h",':')  ;
    console.log(newH);
    console.log(date)
  
//     const date2 = date.replace(date.slice(10,19), `${" "+item.horraire}:00 `.replace("h",':'  ).replace("/","-").replace("/","-"))
//  console.log(date2.split("/").join(":").split (" ").reverse().join("")) 
//  console.log(date2.substring(0,10))
    gsap.to(".anim", {
         opacity: 0,
         x: "-100%",
         duration: 1,
         onComplete: (item) => {
          dispatch(changeValueHoraire(true));
        },
        });
  };
    return (
        <>
        <Box
          className="anim"
          elevation={24}
          sx={{
            display: "flex",
            width: 250,
            flexWrap: "wrap",
          }}
        >
          <Paper
            elevation={24}
            sx={{
              display: "flex",
              justifyContent: "center",
              flexWrap: "wrap",
              width: 250,
              paddingBottom: "20px",
              marginTop: "20px",
              opacity: 0.97,
            }}
          >
            <Box
              sx={{
                height: "max-content",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <h1 style={{ textAlign: "center" }}>Horaire disponnibles</h1>
  
              <p>Selectionner un horaire</p>
            </Box>
            <Box
              sx={{
                display: "flex",
                flexWrap: "wrap",
                justifyContent: "center",
                gap: "10px",
              }}
            >
              {newHeures.map((item) => {
                return (
                  <>
                    {item.count < 3 ? (
                      <Button
                        sx={{
                          marginRight: "20px",
                          backgroundColor: "#323C41",
                          width: 70,
                        }}
                        variant="contained"
                        onClick={() => {
                          setElement(item);
                          animToggle2(item);
                        }}
                      >
                        {item.horraire}
                      </Button>
                    ) : (
                      null
                    )}
                  </>
                );
              })}
            </Box>
          </Paper>
        </Box>
        </>
    );
};

export default Horaires;