import { Box, Paper } from '@mui/material';
import React, {useEffect} from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import {getAllSofts } from '../api/api';
import { animToggle } from '../anim/anim';
import { gsap } from "gsap";
import { useDispatch } from 'react-redux';
import { changeValueSofts } from '../features/toggle.slice';
import { addProduct } from '../features/basket.slice';
const Softs = () => {
    const dispatch = useDispatch();
    const [dataSofts, setDataSofts] = React.useState([]);
    useEffect(() => {
        getAllSofts().then((response) => {
            console.log(response.data);
            setDataSofts(response.data);
        });
    }, []);
    useEffect(() => {
        animToggle();
    }, []);
    const animToggle2 = (item) => {
      const obj = {
        type : "soft",
      }
      const items = {
        ...item,
        ...obj
      }
      dispatch(addProduct(items));

        gsap.to('.anim',{
            duration: 0.5,
            x:'-100%',
            opacity: 0,
            onComplete: () => {
                dispatch(changeValueSofts(false));
            }


        })
    }


    return (
<Box className='anim' sx={
          {
            display: 'flex',
            backgroundColor: "transparent",
            position: 'absolute',
            zIndex: 100,
          }
        }  >
        <Paper elevation={24} sx={
          {
            display: 'flex',
            gap : '20px',
            backgroundColor: "transparent",
            flexWrap: 'wrap',
          }
        }>
            {dataSofts.map((item, index) => (
            <Card sx={{ maxWidth: 345 }}>
            <CardMedia
              component="img"
              height="140"
              image="https://www.bentoburo.com/69-large_default/canettes-coca-cola.jpg"
              alt="green iguana"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {item.name}
              </Typography>
              <Typography variant="body2" color="text.secondary">
              </Typography>
            </CardContent>
            <CardActions>
              <Button size="small"  onClick={()=> animToggle2(item) } >Ajouter au panier</Button>
      
            </CardActions>
          </Card>
            ))}
            
            </Paper>
    </Box>
    );
};

export default Softs;